.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.circularPallet{
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
}
.circularPallet:hover{
  cursor: pointer;
}
.blue{
  background-color: rgb(59, 59, 135);
}
.green{
  background-color: rgb(104, 187, 104);
}
.red{
  background-color: rgb(159, 72, 72);
}
.yellow{
  background-color: rgb(189, 189, 112);
}
.pallet{
  display: flex;
}

